const APIS_STRINGS:STRINGS = {
    latency         : 'latency',
    all             : 'getPredTabData',
    real            : 'getRealDataByIP',
    pred            : 'getPredDataByIP',
    wlan            : 'wlan',
}

/**
 * Esta Función sirve para que hayan varios proyectos a la vez dentro del mismo Smartlink
 * Se llama desde PrincipalPageView, donde se selecciona el proyecto a verse, y cambia los valores de la API
 * Para este caso solo hay de Cananea y de Colahuasi
 * @param valor Valor asignado para cada constante
 */
export function changeURL (valor:number){
    finalURL.value = (valor === 0 ? process.env.VUE_APP_URL_APIS_CANANEA : process.env.VUE_APP_URL_APIS_COLLAHUASI)??'';
}


export default {
    APIS,
    APIS_STRINGS,
}