import { STRINGS } from "@/interfaces/SmartLink/General";
import { ref } from 'vue'

const COLOR_HTML = {
    compare1    : '#FF0033',
    compare2    : '#00CCFF',
    compare3    : '#1F28E9',
    compare13   : '#B400FE',
    compare14   : '#04FA6D',
    green       : '#427739',
    yellow      : '#F9DC4B',
    red         : '#E04637',
    graytrue    : '#CAC8C8',
    grey        : '#00A5E8',
    single      : '#00CCFF',
    drawer      : '#3A9B6B',
}

const COLORS_IN_HTML = ref({
    drawer : '#3A9B6B'
})

const COLOR_CHIP:STRINGS = {
    OK              : '#4CAF50',
    ALERT           : 'yellow',
    ALARM           : 'red',
    PREDICT         : '#0047AB',
    'PREDICT SNR'   : '#0047AB',
    DOWN            : '#9E9E9E',
    LTE             : 'purple'
}

export function changeColor (color:string){
    COLORS_IN_HTML.value.drawer = color;
}


export default {
    COLOR_HTML,
    COLOR_CHIP,
    COLORS_IN_HTML
}